/* eslint-disable */
// Manager Catalog  rich inventory view with photos, stock states, who-has-what, locations, cleaning state, and scanner entry
// Tailwind only, default export = CatalogBoard

import React, { useMemo, useState, useEffect } from 'react'
import { useNavigate } from 'react-router-dom'
import { getImageUrl, handleImageError } from '../utils/imageHelper'
import BarcodeScanner from '../components/BarcodeScanner'

const BACKEND_URL = process.env.REACT_APP_BACKEND_URL || ''

/************* utils *************/
const cls = (...a)=> a.filter(Boolean).join(' ')
const fmtUA = (n)=> (Number(n)||0).toLocaleString('uk-UA', {maximumFractionDigits:2})
const todayISO = ()=> new Date().toISOString().slice(0,10)
const addDays = (iso, d)=> { const x=new Date(iso); x.setDate(x.getDate()+d); return x.toISOString().slice(0,10) }

const STATE = {
  ok:{ label:' ', tone:'corp-badge corp-badge-success' },
  fragile:{ label:'', tone:'corp-badge corp-badge-primary' },
  damaged:{ label:'', tone:'corp-badge corp-badge-error' },
}
const CLEAN = {
  clean:{ label:'', tone:'bg-emerald-50 text-emerald-700 border-emerald-200' },
  wash:{ label:' ', tone:'corp-badge corp-badge-info' },
  dry:{ label:'', tone:'corp-badge corp-badge-warning' },
  repair:{ label:'', tone:'bg-fuchsia-100 text-fuchsia-700 border-fuchsia-200' },
}

/************* small UI *************/
function Badge({tone, children}){
  return <span className={cls('inline-flex items-center gap-1 rounded-full border px-2 py-0.5 text-xs', tone)}>{children}</span>
}
function Pill({onClick, children, tone='slate'}){
  const tones={
    slate:'corp-btn corp-btn-secondary',
    green:'corp-btn corp-btn-primary',
    blue:'corp-btn corp-btn-primary',
    amber:'bg-amber-500 hover:bg-amber-600 text-corp-text-dark'
  }
  return <button onClick={onClick} className={cls('rounded-full px-3 py-1 text-sm', tones[tone])}>{children}</button>
}
function Card({title,right,children}){
  return (
    <div className="rounded-2xl border border-slate-200 bg-white p-4">
      <div className="mb-3 flex items-center justify-between"><h3 className="text-base font-semibold">{title}</h3>{right}</div>
      {children}
    </div>
  )
}

/************* search / filters *************/
function Filters({q,setQ, cat,setCat, state,setState, clean,setClean, categories}){
  return (
    <div className="space-y-3">
      {/*   */}
      <div className="w-full">
        <label className="text-xs text-corp-text-muted font-medium"> ( / SKU / )</label>
        <input 
          value={q} 
          onChange={e=>setQ(e.target.value)} 
          placeholder="    " 
          className="mt-1 w-full rounded-xl border border-slate-300 px-4 py-2.5 focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent" 
        />
      </div>
      
      {/*     */}
      <div className="flex flex-wrap gap-3 items-end">
        <div className="min-w-[180px]">
          <label className="text-xs text-corp-text-muted font-medium"></label>
          <select className="mt-1 w-full rounded-xl border border-slate-300 px-3 py-2" value={cat} onChange={e=>setCat(e.target.value)}>
            <option value="all"></option>
            {categories.map(c => (
              <option key={c} value={c}>{c}</option>
            ))}
          </select>
        </div>
        <div className="min-w-[150px]">
          <label className="text-xs text-corp-text-muted font-medium"></label>
          <select className="mt-1 w-full rounded-xl border border-slate-300 px-3 py-2" value={state} onChange={e=>setState(e.target.value)}>
            <option value="all"></option>
            <option value="ok"> </option>
            <option value="fragile"></option>
            <option value="damaged"></option>
          </select>
        </div>
        <div className="min-w-[150px]">
          <label className="text-xs text-corp-text-muted font-medium"></label>
          <select className="mt-1 w-full rounded-xl border border-slate-300 px-3 py-2" value={clean} onChange={e=>setClean(e.target.value)}>
            <option value="all"></option>
            <option value="clean"></option>
            <option value="wash"> </option>
            <option value="dry"></option>
            <option value="repair"></option>
          </select>
        </div>
        <div className="flex gap-2 ml-auto">
          <Pill tone='blue' onClick={()=>alert('   ()')}></Pill>
          <Pill tone='green' onClick={()=>alert('  ()')}> </Pill>
        </div>
      </div>
      
      {/* Quick Filters */}
      <div className="flex items-center gap-2 flex-wrap">
        <span className="text-xs text-corp-text-muted font-medium"> :</span>
        <button
          onClick={() => {setClean('repair'); setState('all')}}
          className={cls(
            'rounded-full px-3 py-1 text-xs font-medium border transition-colors',
            clean === 'repair' 
              ? 'bg-fuchsia-100 text-fuchsia-700 border-fuchsia-300' 
              : 'bg-white text-corp-text-main border-slate-200 hover:bg-slate-50'
          )}
        >
            
        </button>
        <button
          onClick={() => {setClean('wash'); setState('all')}}
          className={cls(
            'rounded-full px-3 py-1 text-xs font-medium border transition-colors',
            clean === 'wash' 
              ? 'bg-sky-100 text-sky-700 border-sky-300' 
              : 'bg-white text-corp-text-main border-slate-200 hover:bg-slate-50'
          )}
        >
            
        </button>
        <button
          onClick={() => {setState('damaged'); setClean('all')}}
          className={cls(
            'rounded-full px-3 py-1 text-xs font-medium border transition-colors',
            state === 'damaged' 
              ? 'bg-rose-100 text-rose-700 border-rose-300' 
              : 'bg-white text-corp-text-main border-slate-200 hover:bg-slate-50'
          )}
        >
           
        </button>
        <button
          onClick={() => {setState('all'); setClean('all'); setCat('all')}}
          className="rounded-full px-3 py-1 text-xs font-medium border bg-white text-corp-text-main border-slate-200 hover:bg-slate-50 transition-colors"
        >
            
        </button>
      </div>
    </div>
  )
}

/************* table *************/
function Table({rows, onOpen, loading}){
  if (loading) {
    return (
      <div className="rounded-2xl border border-slate-200 bg-white p-12 text-center">
        <div className="text-corp-text-muted"> ...</div>
      </div>
    )
  }

  return (
    <div className="overflow-hidden rounded-2xl border border-slate-200">
      <table className="min-w-full text-sm">
        <thead className="bg-slate-50 text-left text-corp-text-muted">
          <tr>
            <th className="px-3 py-2"></th>
            <th className="px-3 py-2">SKU / </th>
            <th className="px-3 py-2"></th>
            <th className="px-3 py-2"></th>
            <th className="px-3 py-2"> </th>
            <th className="px-3 py-2"></th>
            <th className="px-3 py-2"> </th>
            <th className="px-3 py-2"></th>
            <th className="px-3 py-2 text-right"></th>
          </tr>
        </thead>
        <tbody>
          {rows.map(p=> (
            <tr key={p.id} className="border-t hover:bg-slate-50/50">
              <td className="px-3 py-2">
                {p.cover ? (
                  <img 
                    src={getImageUrl(p.cover)} 
                    alt={p.name} 
                    className="h-12 w-18 rounded-md object-cover bg-slate-100" 
                    onError={handleImageError}
                  />
                ) : (
                  <div className="h-12 w-18 rounded-md bg-slate-100 flex items-center justify-center text-xl"></div>
                )}
              </td>
              <td className="px-3 py-2">
                <div className="font-medium text-corp-text-dark">{p.sku}</div>
                <div className="text-xs text-corp-text-muted">{p.name}</div>
              </td>
              <td className="px-3 py-2">{p.cat}</td>
              <td className="px-3 py-2">
                <div className="flex flex-wrap gap-1">
                  <Badge tone={STATE.ok.tone}> {p.total}</Badge>
                  <Badge tone={STATE.ok.tone}> {p.available}</Badge>
                  <Badge tone={STATE.fragile.tone}> {p.reserved}</Badge>
                  <Badge tone={STATE.damaged.tone}>  {p.rented}</Badge>
                </div>
              </td>
              <td className="px-3 py-2">
                <div className="space-y-1">
                  {p.due_back.map(d=> (
                    <div key={d.order_id} className="text-xs text-corp-text-main">#{d.order_id}  {d.customer}  {d.qty}   : {d.date}</div>
                  ))}
                  {p.due_back.length===0 && <div className="text-xs text-slate-400"></div>}
                </div>
              </td>
              <td className="px-3 py-2">
                {p.in_restore > 0 ? (
                  <Badge tone={CLEAN.repair.tone}> {p.in_restore} </Badge>
                ) : (
                  <div className="text-xs text-slate-400"></div>
                )}
              </td>
              <td className="px-3 py-2">
                <div className="text-xs"> {p.location.zone}   {p.location.aisle}   {p.location.shelf}   {p.location.bin}</div>
              </td>
              <td className="px-3 py-2">
                <Badge tone={CLEAN[p.cleaning.status].tone}>{CLEAN[p.cleaning.status].label}</Badge>
                <div className="text-xs text-corp-text-muted">. .: {p.cleaning.last}</div>
              </td>
              <td className="px-3 py-2 text-right">
                <button onClick={()=>onOpen(p)} className="rounded-full bg-slate-900 px-3 py-1 text-sm text-white"></button>
              </td>
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  )
}

/************* drawer *************/
function Drawer({open, item, onClose, onSave}){
  const [editing, setEditing] = useState(false)
  const [editData, setEditData] = useState({})
  const [damageHistory, setDamageHistory] = useState([])
  const [loadingHistory, setLoadingHistory] = useState(false)
  
  useEffect(() => {
    if (item) {
      setEditData({
        location: {...item.location},
        cleaning: {...item.cleaning},
        state: item.state
      })
      //   
      loadDamageHistory(item.sku)
    }
  }, [item])
  
  const loadDamageHistory = async (sku) => {
    if (!sku) return
    
    try {
      setLoadingHistory(true)
      const response = await fetch(`${BACKEND_URL}/api/product-damage-history/sku/${sku}`)
      const data = await response.json()
      setDamageHistory(data.history || [])
    } catch (error) {
      console.error('Error loading damage history:', error)
      setDamageHistory([])
    } finally {
      setLoadingHistory(false)
    }
  }
  
  if(!open || !item) return null
  
  const handleSave = async () => {
    try {
      await onSave(item.id, editData)
      setEditing(false)
    } catch (error) {
      console.error('Error saving:', error)
      alert(' ')
    }
  }
  
  return (
    <div className="fixed inset-0 z-30 flex">
      <div className="h-full w-full bg-black/30" onClick={onClose}/>
      <div className="absolute right-0 top-0 h-full w-full max-w-2xl overflow-y-auto rounded-l-2xl bg-white p-5 shadow-xl">
        <div className="mb-3 flex items-center justify-between">
          <div>
            <div className="text-lg font-semibold">{item.name}</div>
            <div className="text-xs text-corp-text-muted">{item.sku}  {item.cat}</div>
          </div>
          <div className="flex gap-2">
            {editing ? (
              <>
                <button onClick={handleSave} className="rounded-full bg-green-600 px-3 py-1 text-sm text-white"></button>
                <button onClick={()=>setEditing(false)} className="rounded-full bg-slate-400 px-3 py-1 text-sm text-white"></button>
              </>
            ) : (
              <>
                <button onClick={()=>setEditing(true)} className="rounded-full bg-blue-600 px-3 py-1 text-sm text-white"></button>
                <button onClick={onClose} className="rounded-full bg-slate-900 px-3 py-1 text-sm text-white"></button>
              </>
            )}
          </div>
        </div>

        <div className="grid grid-cols-1 gap-4 md:grid-cols-3">
          <img src={getImageUrl(item.cover)} alt={item.name} className="md:col-span-1 h-32 w-full rounded-xl object-cover" onError={handleImageError}/>
          <div className="md:col-span-2 space-y-2">
            <div className="flex flex-wrap gap-2">
              <Badge tone={STATE.ok.tone}> {item.total}</Badge>
              <Badge tone={STATE.ok.tone}> {item.available}</Badge>
              <Badge tone={STATE.fragile.tone}> {item.reserved}</Badge>
              <Badge tone={STATE.damaged.tone}>  {item.rented}</Badge>
              <Badge tone={STATE[item.state].tone}>{STATE[item.state].label}</Badge>
              <Badge tone={CLEAN[item.cleaning.status].tone}>{CLEAN[item.cleaning.status].label}</Badge>
            </div>
            <div className="grid grid-cols-1 gap-2 md:grid-cols-2">
              <Card title="">
                {editing ? (
                  <div className="space-y-2">
                    <div className="grid grid-cols-2 gap-2">
                      <input value={editData.location.zone} onChange={e=>setEditData({...editData, location:{...editData.location, zone:e.target.value}})} placeholder="" className="rounded border px-2 py-1 text-sm"/>
                      <input value={editData.location.aisle} onChange={e=>setEditData({...editData, location:{...editData.location, aisle:e.target.value}})} placeholder="" className="rounded border px-2 py-1 text-sm"/>
                      <input value={editData.location.shelf} onChange={e=>setEditData({...editData, location:{...editData.location, shelf:e.target.value}})} placeholder="" className="rounded border px-2 py-1 text-sm"/>
                      <input value={editData.location.bin} onChange={e=>setEditData({...editData, location:{...editData.location, bin:e.target.value}})} placeholder="" className="rounded border px-2 py-1 text-sm"/>
                    </div>
                  </div>
                ) : (
                  <>
                    <div className="text-sm"> {item.location.zone} /  {item.location.aisle} /  {item.location.shelf} /  {item.location.bin}</div>
                    <div className="mt-2 flex gap-2">
                      <button className="rounded-md border px-2 py-1 text-xs" onClick={()=>alert('  ()')}></button>
                    </div>
                  </>
                )}
              </Card>
              <Card title=" / ">
                {editing ? (
                  <div className="space-y-2">
                    <select value={editData.cleaning.status} onChange={e=>setEditData({...editData, cleaning:{...editData.cleaning, status:e.target.value}})} className="w-full rounded border px-2 py-1 text-sm">
                      <option value="clean"></option>
                      <option value="wash"> </option>
                      <option value="dry"></option>
                      <option value="repair"></option>
                    </select>
                    <select value={editData.state} onChange={e=>setEditData({...editData, state:e.target.value})} className="w-full rounded border px-2 py-1 text-sm">
                      <option value="ok"> </option>
                      <option value="fragile"></option>
                      <option value="damaged"></option>
                    </select>
                  </div>
                ) : (
                  <>
                    <div className="text-sm">: {CLEAN[item.cleaning.status].label}</div>
                    <div className="text-xs text-corp-text-muted"> : {item.cleaning.last}</div>
                  </>
                )}
              </Card>
            </div>
          </div>
        </div>

        <div className="mt-4 grid grid-cols-1 gap-4 md:grid-cols-2">
          <Card title="  / ">
            <div className="space-y-2 text-sm">
              {item.due_back.map(d => (
                <div key={d.order_id} className="flex items-center justify-between rounded-lg border px-2 py-1">
                  <div>#{d.order_id}  {d.customer}  {d.qty} </div>
                  <div className="text-xs text-corp-text-muted"> {d.date}</div>
                </div>
              ))}
              {item.due_back.length===0 && <div className="text-sm text-corp-text-muted"></div>}
            </div>
          </Card>
          <Card title=" / ">
            <div className="space-y-2">
              {item.barcodes.map(code => (
                <div key={code} className="flex items-center justify-between rounded-lg border px-2 py-1 text-sm">
                  <div>{code}</div>
                  <div className="flex items-center gap-2">
                    <button className="rounded-md border px-2 py-0.5 text-xs" onClick={()=>alert('  ()')}></button>
                    <button className="rounded-md border px-2 py-0.5 text-xs" onClick={()=>alert('  ()')}>Move</button>
                  </div>
                </div>
              ))}
              {item.barcodes.length===0 && <div className="text-sm text-corp-text-muted"> </div>}
            </div>
          </Card>
          <Card title=" / ">
            <div className="flex flex-wrap gap-2 text-sm">
              {item.variants && item.variants.map(v => (
                <span key={v.code} className="rounded-md border px-2 py-1">{v.label}</span>
              ))}
              {(!item.variants || item.variants.length === 0) && <div className="text-sm text-corp-text-muted"> </div>}
            </div>
          </Card>
          <Card title={`   (${damageHistory.length})`}>
            {loadingHistory ? (
              <div className="text-sm text-slate-400">...</div>
            ) : damageHistory.length > 0 ? (
              <div className="max-h-48 space-y-2 overflow-y-auto text-sm">
                {damageHistory.map(d => (
                  <div key={d.id} className="rounded-lg border p-2">
                    <div className="flex items-start justify-between">
                      <div>
                        <div className="font-medium text-corp-text-dark">{d.damage_type}</div>
                        <div className="text-xs text-corp-text-muted">
                          {d.stage_label}   #{d.order_number}
                        </div>
                        {d.note && <div className="mt-1 text-xs text-corp-text-main">{d.note}</div>}
                      </div>
                      <div className="text-right">
                        <div className={`text-sm font-semibold ${d.severity === 'high' ? 'text-red-600' : d.severity === 'medium' ? 'text-amber-600' : 'text-green-600'}`}>
                          {fmtUA(d.fee)}
                        </div>
                        <div className="text-xs text-slate-400">
                          {new Date(d.created_at).toLocaleDateString('uk-UA')}
                        </div>
                      </div>
                    </div>
                  </div>
                ))}
              </div>
            ) : (
              <div className="text-sm text-corp-text-muted">   </div>
            )}
          </Card>
        </div>
      </div>
    </div>
  )
}

/************* main *************/
export default function CatalogBoard(){
  const [products, setProducts] = useState([])
  const [loading, setLoading] = useState(true)
  const [q,setQ] = useState('')
  const [cat,setCat] = useState('all')
  const [state,setState] = useState('all')
  const [clean,setClean] = useState('all')
  const [drawer,setDrawer] = useState({open:false,item:null})
  const [familyModal, setFamilyModal] = useState({open: false, families: [], selectedFamily: null})
  const [selectedProducts, setSelectedProducts] = useState([])
  const [familySearch, setFamilySearch] = useState('') //   
  const [draggedProduct, setDraggedProduct] = useState(null) //  drag&drop
  const [scannerOpen, setScannerOpen] = useState(false)
  const navigate = useNavigate()

  // Load products from backend
  useEffect(() => {
    const loadCatalog = async () => {
      try {
        setLoading(true)
        console.log('[Catalog] Loading from:', BACKEND_URL)
        
        // Build search param
        let url = `${BACKEND_URL}/api/catalog?include_reservations=true`  //  
        if (q) {
          url += `&search=${encodeURIComponent(q)}`
        }
        
        console.log('[Catalog] Fetching:', url)
        
        const response = await fetch(url, {
          method: 'GET',
          headers: { 
            'Content-Type': 'application/json',
            'Accept': 'application/json'
          },
          mode: 'cors',
          credentials: 'omit'
        })
        
        console.log('[Catalog] Response status:', response.status)
        
        if (!response.ok) {
          const errorText = await response.text()
          console.error('[Catalog] Error response:', errorText)
          throw new Error(`HTTP error! status: ${response.status}`)
        }
        
        const data = await response.json()
        console.log('[Catalog] Loaded products:', data.length)
        setProducts(data)
      } catch (error) {
        console.error('[Catalog] Error loading catalog:', error)
        alert(`  : ${error.message}`)
      } finally {
        setLoading(false)
      }
    }

    loadCatalog()
  }, [q])
  
  // Save product changes
  const handleSaveProduct = async (productId, data) => {
    try {
      console.log('[Catalog] Saving product:', productId, data)
      
      const response = await fetch(`${BACKEND_URL}/api/catalog/${productId}`, {
        method: 'PUT',
        headers: { 
          'Content-Type': 'application/json',
          'Accept': 'application/json'
        },
        mode: 'cors',
        credentials: 'omit',
        body: JSON.stringify(data)
      })
      
      console.log('[Catalog] Save response status:', response.status)
      
      if (!response.ok) {
        const errorText = await response.text()
        console.error('[Catalog] Save error:', errorText)
        throw new Error(`HTTP error! status: ${response.status}`)
      }
      
      // Reload catalog
      const catalogResponse = await fetch(`${BACKEND_URL}/api/catalog?include_reservations=true`, {
        method: 'GET',
        headers: { 
          'Content-Type': 'application/json',
          'Accept': 'application/json'
        },
        mode: 'cors',
        credentials: 'omit'
      })
      const catalogData = await catalogResponse.json()
      setProducts(catalogData)
      
      // Update drawer item
      const updatedItem = catalogData.find(p => p.id === productId)
      if (updatedItem) {
        setDrawer({open: true, item: updatedItem})
      }
      
      alert('   ')
    } catch (error) {
      console.error('[Catalog] Error saving product:', error)
      alert(` : ${error.message}`)
      throw error
    }
  }

  // Get unique categories
  const categories = useMemo(() => {
    const cats = [...new Set(products.map(p => p.category_name || p.cat).filter(Boolean))]
    return cats.sort()
  }, [products])

  const rows = useMemo(()=> products.filter(p=>{
    const okC = cat==='all' || p.cat===cat || p.category_name===cat
    const okS = state==='all' || p.state===state
    const okCl = clean==='all' || p.cleaning.status===clean
    return okC && okS && okCl
  }),[products,cat,state,clean])
  
  //    
  const inRestoreCount = useMemo(() => {
    return products.filter(p => p.cleaning?.status === 'repair').length
  }, [products])

  //   
  const openFamilyManager = async () => {
    try {
      const res = await fetch(`${BACKEND_URL}/api/catalog/families`)
      const data = await res.json()
      setFamilyModal({open: true, families: data, selectedFamily: null})
    } catch (err) {
      console.error('Error loading families:', err)
      setFamilyModal({open: true, families: [], selectedFamily: null})
    }
  }

  //   
  const createFamily = async () => {
    const name = prompt(' :')
    if (!name) return
    
    const description = prompt('  ():')
    
    try {
      const res = await fetch(`${BACKEND_URL}/api/catalog/families`, {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ name, description })
      })
      if (!res.ok) throw new Error('Failed to create family')
      alert('  !')
      openFamilyManager() //  
    } catch (err) {
      console.error('Error creating family:', err)
      alert('   ')
    }
  }

  // '   
  const assignToFamily = async (familyId) => {
    if (selectedProducts.length === 0) {
      alert('   \'')
      return
    }

    try {
      const res = await fetch(`${BACKEND_URL}/api/catalog/families/${familyId}/assign`, {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ product_ids: selectedProducts })
      })
      if (!res.ok) throw new Error('Failed to assign products')
      alert(` ${selectedProducts.length}  '  !`)
      setSelectedProducts([])
      //    
      const loadRes = await fetch(`${BACKEND_URL}/api/catalog?include_reservations=true`)
      const productsData = await loadRes.json()
      setProducts(productsData)
      await openFamilyManager()
    } catch (err) {
      console.error('Error assigning products:', err)
      alert('  \'')
    }
  }

  // '   
  const removeFromFamily = async (productId) => {
    try {
      const res = await fetch(`${BACKEND_URL}/api/catalog/products/${productId}/remove-family`, {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' }
      })
      if (!res.ok) throw new Error('Failed to remove product')
      alert('    !')
      //    
      const loadRes = await fetch(`${BACKEND_URL}/api/catalog?include_reservations=true`)
      const productsData = await loadRes.json()
      setProducts(productsData)
      await openFamilyManager()
    } catch (err) {
      console.error('Error removing product:', err)
      alert(' ')
    }
  }

  return (
    <div className="mx-auto max-w-7xl p-6">
      <div className="mb-4 flex items-center justify-between">
        <div className="flex items-center gap-3">
          <h1 className="text-2xl font-bold"> / </h1>
          {inRestoreCount > 0 && (
            <Badge tone={CLEAN.repair.tone}>
               {inRestoreCount}  
            </Badge>
          )}
        </div>
        <div className="flex gap-2">
          <button 
            className="rounded-full bg-blue-600 text-white px-3 py-1 text-sm font-medium hover:bg-blue-700 flex items-center gap-1" 
            onClick={() => setScannerOpen(true)}
          >
              SKU
          </button>
          <button className="rounded-full bg-purple-500 text-white px-3 py-1 text-sm font-medium hover:bg-purple-600" onClick={openFamilyManager}>  </button>
          <button className="rounded-full bg-slate-200 px-3 py-1 text-sm" onClick={()=>navigate('/')}> </button>
          <button className="rounded-full bg-slate-200 px-3 py-1 text-sm" onClick={()=>alert(' CSV ()')}></button>
          <button className="rounded-full bg-slate-200 px-3 py-1 text-sm" onClick={()=>alert(' CSV ()')}></button>
        </div>
      </div>
      
      {/* Barcode Scanner */}
      <BarcodeScanner
        isOpen={scannerOpen}
        onClose={() => setScannerOpen(false)}
        onScan={(code) => {
          console.log('[Catalog] Scanned:', code)
          // Set search query to scanned code
          setQ(code)
          setScannerOpen(false)
        }}
        title=" SKU "
      />

      <Filters q={q} setQ={setQ} cat={cat} setCat={setCat} state={state} setState={setState} clean={clean} setClean={setClean} categories={categories} />

      <div className="mt-4">
        <Table rows={rows} onOpen={(item)=>setDrawer({open:true,item})} loading={loading} />
      </div>

      <Drawer open={drawer.open} item={drawer.item} onClose={()=>setDrawer({open:false,item:null})} onSave={handleSaveProduct} />
      
      {/* Modal -   */}
      {familyModal.open && (
        <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50 p-4">
          <div className="bg-white rounded-2xl max-w-5xl w-full max-h-[90vh] overflow-hidden flex flex-col p-6">
            <div className="flex items-center justify-between mb-4">
              <h2 className="text-2xl font-bold">  </h2>
              <button onClick={() => setFamilyModal({...familyModal, open: false})} className="text-slate-400 hover:text-corp-text-main text-2xl"></button>
            </div>

            {/*    */}
            <button 
              onClick={createFamily}
              className="mb-4 px-4 py-2 bg-emerald-500 text-white rounded-lg font-medium hover:bg-emerald-600"
            >
              +   
            </button>

            <div className="flex-1 overflow-auto">
              {/*   */}
              <div className="space-y-4 mb-6">
                {familyModal.families.length === 0 ? (
                  <div className="text-center text-corp-text-muted py-8">
                     .   !
                  </div>
                ) : (
                  familyModal.families.map(family => (
                    <div key={family.id} className="border border-slate-200 rounded-lg p-4 bg-slate-50">
                      <div className="flex items-center justify-between mb-2">
                        <h3 className="font-semibold text-lg">{family.name}</h3>
                        <div className="flex gap-2">
                          <button 
                            onClick={() => assignToFamily(family.id)}
                            disabled={selectedProducts.length === 0}
                            className={cls(
                              "px-3 py-1 rounded text-sm transition-colors",
                              selectedProducts.length > 0 
                                ? "bg-blue-500 text-white hover:bg-blue-600" 
                                : "bg-slate-300 text-corp-text-muted cursor-not-allowed"
                            )}
                          >
                            '  ({selectedProducts.length})
                          </button>
                          <button 
                            onClick={async () => {
                              if (!confirm(`  "${family.name}"?`)) return
                              try {
                                const res = await fetch(`${BACKEND_URL}/api/catalog/families/${family.id}`, {
                                  method: 'DELETE'
                                })
                                if (!res.ok) throw new Error('Failed to delete family')
                                alert('  !')
                                await openFamilyManager()
                              } catch (err) {
                                console.error('Error deleting family:', err)
                                alert('   ')
                              }
                            }}
                            className="px-3 py-1 rounded text-sm bg-rose-500 text-white hover:bg-rose-600 transition-colors"
                          >
                             
                          </button>
                        </div>
                      </div>
                      {family.description && (
                        <p className="text-sm text-corp-text-main mb-2">{family.description}</p>
                      )}
                      
                      {/*    */}
                      <div className="mt-3">
                        <div className="text-sm font-medium text-slate-700 mb-2">  :</div>
                        <div 
                          className={cls(
                            "min-h-[100px] rounded-lg p-2 transition-colors",
                            family.products && family.products.length > 0 
                              ? "grid grid-cols-2 gap-2" 
                              : "border-2 border-dashed border-slate-300 flex items-center justify-center"
                          )}
                          onDragOver={(e) => {
                            e.preventDefault()
                            e.currentTarget.classList.add('bg-blue-50', 'border-blue-400')
                          }}
                          onDragLeave={(e) => {
                            e.currentTarget.classList.remove('bg-blue-50', 'border-blue-400')
                          }}
                          onDrop={async (e) => {
                            e.preventDefault()
                            e.currentTarget.classList.remove('bg-blue-50', 'border-blue-400')
                            
                            const productId = e.dataTransfer.getData('productId')
                            if (productId) {
                              try {
                                const res = await fetch(`${BACKEND_URL}/api/catalog/families/${family.id}/assign`, {
                                  method: 'POST',
                                  headers: { 'Content-Type': 'application/json' },
                                  body: JSON.stringify({ product_ids: [parseInt(productId)] })
                                })
                                if (!res.ok) throw new Error('Failed to assign product')
                                
                                // Reload families
                                const familiesRes = await fetch(`${BACKEND_URL}/api/catalog/families`)
                                const familiesData = await familiesRes.json()
                                setFamilyModal({...familyModal, families: familiesData})
                                
                                alert('    !')
                              } catch (err) {
                                console.error('Error assigning product:', err)
                                alert('   ')
                              }
                            }
                          }}
                        >
                          {family.products && family.products.length > 0 ? (
                            family.products.map(prod => (
                              <div 
                                key={prod.product_id} 
                                className="flex items-center gap-2 p-2 bg-white rounded border border-slate-200 hover:shadow-sm transition-shadow"
                              >
                                {prod.cover ? (
                                  <img 
                                    src={getImageUrl(prod.cover)} 
                                    alt={prod.name}
                                    className="w-12 h-12 object-cover rounded bg-slate-100" 
                                    onError={handleImageError}
                                  />
                                ) : null}
                                <div className="w-12 h-12 bg-slate-100 rounded flex items-center justify-center text-2xl" style={{display: 'none'}}>
                                  
                                </div>
                                <div className="flex-1 text-sm">
                                  <div className="font-medium text-corp-text-dark">{prod.name}</div>
                                  <div className="text-xs text-corp-text-muted">SKU: {prod.sku}</div>
                                </div>
                                <button 
                                  onClick={() => removeFromFamily(prod.product_id)}
                                  className="text-rose-500 hover:text-rose-700 hover:bg-rose-50 rounded-full w-6 h-6 flex items-center justify-center text-xl transition-colors"
                                >
                                  
                                </button>
                              </div>
                            ))
                          ) : (
                            <div className="text-sm text-slate-400 text-center py-4">
                              <div className="text-2xl mb-2"></div>
                              <div>  </div>
                              <div className="text-xs">     "'"</div>
                            </div>
                          )}
                        </div>
                      </div>
                    </div>
                  ))
                )}
              </div>

              {/*    ' */}
              <div className="border-t pt-4">
                <div className="flex items-center justify-between mb-3">
                  <h3 className="font-semibold">   ':</h3>
                  {selectedProducts.length > 0 && (
                    <button 
                      onClick={() => setSelectedProducts([])}
                      className="text-sm text-corp-text-muted hover:text-slate-700"
                    >
                        ({selectedProducts.length})
                    </button>
                  )}
                </div>
                
                {/*   SKU */}
                <div className="mb-3">
                  <input 
                    type="text"
                    value={familySearch}
                    onChange={(e) => setFamilySearch(e.target.value)}
                    placeholder="   SKU  ..."
                    className="w-full px-3 py-2 border border-slate-300 rounded-lg text-sm focus:outline-none focus:ring-2 focus:ring-blue-500"
                  />
                </div>

                <div className="grid grid-cols-3 gap-2 max-h-64 overflow-auto bg-slate-50 p-3 rounded-lg">
                  {products
                    .filter(p => {
                      if (!familySearch) return true
                      const search = familySearch.toLowerCase()
                      return p.sku?.toLowerCase().includes(search) || p.name?.toLowerCase().includes(search)
                    })
                    .sort((a, b) => (b.product_id || 0) - (a.product_id || 0)) //   ID ( )
                    .map(p => (
                      <label 
                        key={p.product_id} 
                        className={cls(
                          "flex items-center gap-2 p-2 border rounded cursor-move transition-all relative",
                          selectedProducts.includes(p.product_id) 
                            ? "bg-blue-50 border-blue-300 shadow-sm" 
                            : "bg-white border-slate-200 hover:bg-slate-50 hover:shadow-md"
                        )}
                        draggable
                        onDragStart={(e) => {
                          e.dataTransfer.setData('productId', p.product_id.toString())
                          e.currentTarget.style.opacity = '0.5'
                        }}
                        onDragEnd={(e) => {
                          e.currentTarget.style.opacity = '1'
                        }}
                      >
                        <input 
                          type="checkbox" 
                          checked={selectedProducts.includes(p.product_id)}
                          onChange={(e) => {
                            if (e.target.checked) {
                              setSelectedProducts([...selectedProducts, p.product_id])
                            } else {
                              setSelectedProducts(selectedProducts.filter(id => id !== p.product_id))
                            }
                          }}
                          className="h-4 w-4 text-blue-600"
                        />
                        {p.cover ? (
                          <img 
                            src={getImageUrl(p.cover)} 
                            alt={p.name}
                            className="w-8 h-8 object-cover rounded bg-slate-100" 
                            onError={handleImageError}
                          />
                        ) : (
                          <div className="w-8 h-8 bg-slate-100 rounded flex items-center justify-center text-xs"></div>
                        )}
                        <div className="text-xs flex-1 min-w-0">
                          <div className="font-medium truncate">{p.name}</div>
                          <div className="text-corp-text-muted">{p.sku}</div>
                        </div>
                      </label>
                    ))}
                </div>
              </div>
            </div>
          </div>
        </div>
      )}
    </div>
  )
}
